/************
4. 1199px
************/

@media only screen and (max-width: 1199px) {
	.header_container {
		padding-left: 45px;
		padding-right: 45px;
	}
	.header_container .container {
		max-width: 100%;
	}
	.main_nav {
		margin-left: 50px;
	}
	.search {
		margin-left: 25px;
	}
	.main_nav > ul > li:not(:last-child) {
		margin-right: 40px;
	}
}

