/************
6. 991px
************/

@media only screen and (max-width: 991px) {
	.header_container {
		padding-left: 15px;
		padding-right: 15px;
	}
	.main_nav,
	.header_social {
		display: none;
	}
	.search {
		display: none;
	}
	.hamburger {
		display: inline-block;
	}
	.home_slider_container {
		padding-left: 30px;
		padding-right: 30px;
	}
	.home_slider_title {
		font-size: 48px;
	}
	.home_slider_dots_container {
		padding-left: 30px;
		padding-right: 30px;
	}
	.avds_container {
		height: auto;
		padding-left: 30px;
		padding-right: 30px;
	}
	.avds_small {
		width: 100%;
		height: 490px;
	}
	.avds_large {
		width: 100%;
		height: 490px;
		margin-top: 30px;
	}
	.product {
		width: calc((100% - 30px) / 2);
	}
	.icon_box_col:not(:last-child) {
		margin-bottom: 60px;
	}
	.icon_box {
		padding-left: 80px;
		padding-right: 80px;
	}
	.footer_overlay {
		height: 200px;
	}
	.footer_content {
		height: 200px;
	}
	.footer_logo,
	.copyright,
	.footer_social {
		text-align: center;
		width: 100%;
	}
	.copyright {
		margin-top: 15px;
	}
	.footer_social {
		margin-top: 21px;
	}
}

