/************
10. 767px
************/

@media only screen and (max-width: 767px) {
	.home_slider_content {
		max-width: 100%;
	}
	.home_slider_container {
		padding-left: 0px;
		padding-right: 0px;
	}
	.home_slider_dots_container {
		display: none;
	}
	.avds_xl_container {
		padding: 30px;
	}
	.avds_xl_content {
		width: 100%;
	}
	.icon_box {
		padding-left: 50px;
		padding-right: 50px;
	}
}

/************
11. 575px
************/

@media only screen and (max-width: 575px) {
	p {
		font-size: 13px;
	}
	.menu {
		width: 100vw;
		right: -100vw;
	}
	.menu_container {
		padding-top: 80px;
	}
	.page_menu_item > a,
	.page_menu_selection li a {
		font-size: 13px;
		height: 40px;
		line-height: 40px;
	}
	.header_container {
		padding-left: 0px;
		padding-right: 0px;
	}
	.header_content {
		height: 70px;
	}
	.logo a,
	.header.scrolled .logo a {
		font-size: 18px;
	}
	.home {
		height: 100vh;
	}
	.home_slider_container {
		padding-left: 0px;
		padding-right: 0px;
		padding-top: 0;
		padding-bottom: 0;
	}
	.home_slider_title {
		font-size: 24px;
	}
	.home_slider_subtitle {
		font-size: 13px;
		margin-top: 15px;
	}
	.button {
		width: 148px;
		height: 41px;
	}
	.button a {
		font-size: 13px;
		line-height: 37px;
	}
	.home_button {
		margin-top: 32px;
	}
	.avds_container {
		padding-left: 15px;
		padding-right: 15px;
	}
	.avds_large_content {
		max-width: calc(100% - 30px);
	}
	.avds_title {
		font-size: 24px;
	}
	.avds_text {
		font-size: 13px;
	}
	.product {
		width: 100%;
	}
	.avds_large_content {
		padding-right: 30px;
	}
	.newsletter_title {
		font-size: 24px;
	}
	.newsletter_button {
		width: 128px;
		height: 41px;
	}
	.newsletter_button span {
		font-size: 13px;
	}
	.copyright {
		font-size: 13px;
	}
	.footer_logo a {
		font-size: 24px;
	}
}

